@font-face {
  font-family: yekan;
  src: url(../src/assets/YekanBakh-Medium.woff) , url(../src/assets/YekanBakh-Medium.woff2);

}
@font-face {
  font-family: yaqut;
  src: url(../src/assets/Yagut.ttf);

}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "yekan", "yaqut" , sans-serif;

  

}
body {
  margin: 0;
  font-family: "yekan", "yaqut" , sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
  background: #F9F8F7;
  overflow-x: hidden;
  font-family: yekan;
}
span{
  font-family: "yaqut" , sans-serif;
  cursor: pointer;
}
body::-webkit-scrollbar {
  width: 5px;
  
}
body::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: #353E55;

}

a {
  text-decoration: none;
  color: black;
}
