.button-container {
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 15px 0 15px;
  font-size: 15px;
  background-color: #353E55;
  color: white;
  text-transform: uppercase;
  font-family: "Roboto Condensed";
  font-weight: bolder;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  border-radius: 10px;
  &:hover {
    background-color: #F9F8F7;
    color: #353E55;
    border: 1px solid #353E55;
  }

  &.google-sign-in,
  &.alert-box {
    background-color: #e27c89;
    color: white;

    &:hover {
      background-color: #df6777;
      border: none;
    }
  }

  &.inverted,
  &.inverted-add-to-cart {
    background-color: #F9F8F7;
    color: #353E55;
    border: 1px solid #353E55;

    &:hover {
      background-color: #353E55;
      color: white;
      border: none;
    }
  }
}
